#App {
  width: 100vw;
  /*height: 100vh;*/
  padding: 0;
  margin: 0;
  display: flex;
}

#divider {
  width: 3px;
  /*height: 100%;*/
  background-color: var(--border-color-b);
  cursor: ew-resize;
  z-index: 10;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.searchInput {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: var(--surface-color-a);
  border-radius: 1rem;
  flex: 6;
  gap: 10px;

  #clear {
    float: right;
  }
}

input {
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  float: left;
  color: var(--text-color);
}


.button,
.icon {
  width: 20px;
  filter: invert(1);
  cursor: pointer;
}

.button:hover {
  transform: scale(1.2);
}

.buttons {
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
}

#borderline {
  width: 95%;
  border: 0.5px groove var(--surface-color-a);
  margin: 10px;
}

.participants {
  width: 100%;
  overflow: hidden;
}
